import React from "react";
import classes from "./ProfileInfo.module.css"
import Preloader from "../../common/preloader/preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile,status,updateStatus}) => {

    if(!profile) {
        return <Preloader />
    }

    return (
        <div>
            <div className={classes.profileBack}>
                <img alt="profileBack" src={process.env.PUBLIC_URL + "/assets/img/back.jpg"}/>
            </div>
            <div>
                <div className={classes.avatar}>
                    <img alt="avatar" src={profile.photos.large != null ? profile.photos.large  : "/assets/img/avatars/default_user.png"}/>
                </div>
                <div className={classes.discription}>
                    <h3>{profile.fullName}</h3>
                    <p>{profile.lookingForAJobDescription}</p>

                    <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />

                    <p>{profile.aboutMe}</p>
                    <div className={classes.social}>

                        {profile.contacts.facebook ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.facebook}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/facebook.png'} /></a> :''}
                        {profile.contacts.github ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.github}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/github.png'} /></a> :''}
                        {profile.contacts.vk ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.vk}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/vk.png'} /></a> :''}
                        {profile.contacts.website ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.website}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/website.png'} /></a> :''}
                        {profile.contacts.youtube ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.youtube}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/youtube.png'} /></a> :''}
                        {profile.contacts.instagram ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.instagram}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/insta.png'} /></a> :''}
                        {profile.contacts.twitter ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={profile.contacts.twitter}><img alt={'icon'} src={process.env.PUBLIC_URL +'/assets/img/twitter.png'} /></a> :''}
                        {profile.contacts.email ? <a className={classes.insta} target="_blank" rel="noopener noreferrer" href={'mailto:' + profile.contacts.email}><img alt={process.env.PUBLIC_URL +'icon'} src={'/assets/img/email.png'} /></a> :''}

                    </div>
                </div>
            </div>
        </div>

)
}

export default ProfileInfo